import React from "react";

import {OraButton, OraMessage, OraLoader} from "eshop/components/OraCommonComponents";
import MulticartCancelOrderPopup from "../../checkout/components/serialNumber/MulticartCancelOrderPopup";



export default class MsisdnNegativeVerificationComponent extends React.Component {

    constructor(props){
        super(props);
        this.state={openConfirmModal:false};

    }
    removeByOmniCode(){
        this.setState({openConfirmModal:false});
        this.props.removeCartByOmni(this.props.cartToRemove)

    }

    openCancelOrderPopup() {
          this.setState({openConfirmModal:true});
        }
    onConfirmModalClose() {
        this.setState({openConfirmModal:false});
    }


    render() {
        return (
           <div className="opl-console">
               <div className="u-spacing-left-l">
                   <OraMessage type="error" className="g-redf-c"> <span dangerouslySetInnerHTML={{__html: this.props.description}} /></OraMessage>
                   <div className="l-row u-spacing-top-l u-spacing-bottom-l">
                       <OraButton modal={false} type="text" size="medium"
                            onClick={this.props.reset}
                       >Zweryfikuj inny numer</OraButton>
                   </div>
                    {this.props.cartToRemove&&
                      <div className="l-row u-spacing-top-l u-spacing-bottom-l">
                        <OraButton modal={false} type="text" size="medium"
                            onClick={this.openCancelOrderPopup.bind(this)}
                        >Usuń koszyk</OraButton>
                     </div>}
               </div>
               <MulticartCancelOrderPopup {...this.props} cancelOrderQuestion={"Czy na pewno chcesz usunąć istniejący koszyk dla msisdn: "+this.props.msisdn+" ?"} onConfirm={this.removeByOmniCode.bind(this)} onClose={this.onConfirmModalClose.bind(this)} openPopup={this.state.openConfirmModal} id="remove-order-cart-popup"/>


           </div>
        );
    }
};


